import './App.css';
import {useState, useEffect} from 'react';
import CounterResponsive from './Counter/CounterResponsive';
import ReactGA from 'react-ga';
import moment from 'moment';


function App() {
  const [hour, setHour] = useState();
  const [min, setMin] = useState();
  const [sec, setSec] = useState();

  useEffect(() => {
    const timer = setInterval(() => {
      setHour(new Date().getUTCHours());
      setMin(new Date().getUTCMinutes());
      setSec(new Date().getUTCSeconds());
    }, 1000);

    return () => {
      clearInterval(timer);
    };
  }, []);

  ReactGA.initialize('G-C87SD1QXBX');
  ReactGA.pageview(window.location.pathname + window.location.search);
  return (

    <div>
    <CounterResponsive/>
    <div className="footer">
      <h1 className="footer-title">nicholas zarate</h1>
      <h1 className="footer-info">
        <p className="utc">UTC&nbsp; </p><br></br><p id="hour">{hour}:</p><p id="min">{min}</p>< p id="sec">:{sec}</p>
      </h1>
    </div>
    </div>
  );
}

export default App;
